ï»¿.button-base {
  font-size: 16px;
  padding: 4px 15px 3px 15px;
}

%templete {
  @extend .button-base;
}

.button {
  @extend %templete;
  @apply border-2 border-gray-dark border-solid;

  &:hover {
    @apply bg-gray-dark text-white;
  }
}

.button-primary {
  @extend %templete;
  @apply border border-accent border-solid;
  @apply text-accent;

  &:hover {
    @apply bg-accent-dark text-white;
  }
}

.button-link {
  @extend %templete;
  @apply border-none;
  @apply text-main-dark;

  &:hover {
    @apply text-main-hover;
  }
}
