.input-form {
  @apply flex flex-col;
  max-width: 600px;
}

.form-item {
  @apply flex flex-col;
  padding: 2px 15px 10px 15px;

  > label,
  > legend {
    @apply pb-1;
  }
}

.field-validation-error {
  @apply text-attention;
}

input,
textarea,
select {
  @apply border border-gray-dark rounded-sm;
}

input,
select {
  height: 2.5rem;
}

input[type="text"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="url"],
input[type="email"],
input[type="date"],
input[type="time"],
input[type="number"],
input[type="range"],
input[type="color"],
input[type="datetime"],
input[type="datetime-local"],
input[type="month"],
input[type="week"],
textarea,
select {
  @apply w-full;
  padding: 4px 10px 3px 10px;
}

input[type="checkbox"],
input[type="radio"] {
  @apply relative;
  @apply outline-none cursor-pointer;
  top: 4px;
  height: 20px;
  width: 20px;
  ~ span {
    @apply cursor-pointer;
    @apply ml-2;
  }
}

input[type="checkbox"]:before,
input[type="radio"]:before {
  @apply block absolute pointer-events-none;
  @apply border-solid border-2 border-main;
  content: "";
  height: 20px;
  width: 20px;
}

input[type="checkbox"]:after,
input[type="radio"]:after {
  @apply block absolute pointer-events-none;
  content: "";
}

input[type="checkbox"]:after {
  height: 20px;
  width: 20px;
}

input[type="radio"]:after {
  top: 4px;
  left: 4px;
  height: 10px;
  width: 10px;
}

input[type="radio"]:before,
input[type="radio"]:after {
  @apply rounded-full;
}

input[type="checkbox"]:checked:after,
input[type="radio"]:checked:after {
  @apply bg-main;
}

input[type="checkbox"]:checked:after {
  background-image: url(/image/check.png);
  background-position: center;
  background-repeat: no-repeat;
}

input[type="hidden"],
input[type="file"] {
}

input[type="submit"],
input[type="reset"],
input[type="button"],
input[type="image"],
button {
}
