@tailwind base;
html {
  font-size: 62.5%;
}

body {
  font-size: 1.6em;
  font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
}

@tailwind components;
h2 {
  @apply font-bold;
  font-size: 18px;
  padding-bottom: 7px;
}

#root {
  @apply min-h-screen;
  @apply grid;
  grid-template-rows: 50px 1fr 30px;
}

#app-header {
  @apply bg-main-dark text-white;
  @apply w-full;
  @apply font-bold;
  padding: 10px 20px 5px 20px;
  font-size: 24px;
}

#app-container {
  @apply grid;
  grid-template-columns: 150px 1fr;
}
#app-container nav {
  @apply bg-main text-white;
  padding-top: 4px;
  padding-left: 20px;
}
#app-container nav header {
  @apply font-bold;
}
#app-container nav > ul > li {
  padding: 5px 0;
}
#app-container nav > ul > li > ul {
  padding-left: 10px;
}
#app-container main {
  padding: 16px;
}

#app-footer {
  @apply bg-main-dark text-white;
  @apply flex items-center;
  @apply pl-4;
}

.horizontal-items {
  @apply flex;
}
.horizontal-items > :not(:first-child) {
  margin-left: 20px;
}
.horizontal-items.right {
  @apply justify-end;
}

.fullwidth {
  @apply w-full;
}

.button-base, .button-link, .button-primary, .button {
  font-size: 16px;
  padding: 4px 15px 3px 15px;
}

.button {
  @apply border-2 border-gray-dark border-solid;
}
.button:hover {
  @apply bg-gray-dark text-white;
}

.button-primary {
  @apply border border-accent border-solid;
  @apply text-accent;
}
.button-primary:hover {
  @apply bg-accent-dark text-white;
}

.button-link {
  @apply border-none;
  @apply text-main-dark;
}
.button-link:hover {
  @apply text-main-hover;
}

.table th {
  padding: 8px 18px;
}
.table thead th {
  @apply bg-main-dark text-white;
  @apply border-b border-white;
}
.table thead th:not(:first-child) {
  @apply border-l border-white;
}
.table tbody th {
  @apply bg-main-light text-main-dark;
  @apply border-r border-main-dark;
}
.table td {
  padding: 6px 10px;
}
.table tr {
  @apply border border-main-dark;
}

.input-form {
  @apply flex flex-col;
  max-width: 600px;
}

.form-item {
  @apply flex flex-col;
  padding: 2px 15px 10px 15px;
}
.form-item > label,
.form-item > legend {
  @apply pb-1;
}

.field-validation-error {
  @apply text-attention;
}

input,
textarea,
select {
  @apply border border-gray-dark rounded-sm;
}

input,
select {
  height: 2.5rem;
}

input[type=text],
input[type=password],
input[type=search],
input[type=tel],
input[type=url],
input[type=email],
input[type=date],
input[type=time],
input[type=number],
input[type=range],
input[type=color],
input[type=datetime],
input[type=datetime-local],
input[type=month],
input[type=week],
textarea,
select {
  @apply w-full;
  padding: 4px 10px 3px 10px;
}

input[type=checkbox],
input[type=radio] {
  @apply relative;
  @apply outline-none cursor-pointer;
  top: 4px;
  height: 20px;
  width: 20px;
}
input[type=checkbox] ~ span,
input[type=radio] ~ span {
  @apply cursor-pointer;
  @apply ml-2;
}

input[type=checkbox]:before,
input[type=radio]:before {
  @apply block absolute pointer-events-none;
  @apply border-solid border-2 border-main;
  content: "";
  height: 20px;
  width: 20px;
}

input[type=checkbox]:after,
input[type=radio]:after {
  @apply block absolute pointer-events-none;
  content: "";
}

input[type=checkbox]:after {
  height: 20px;
  width: 20px;
}

input[type=radio]:after {
  top: 4px;
  left: 4px;
  height: 10px;
  width: 10px;
}

input[type=radio]:before,
input[type=radio]:after {
  @apply rounded-full;
}

input[type=checkbox]:checked:after,
input[type=radio]:checked:after {
  @apply bg-main;
}

input[type=checkbox]:checked:after {
  background-image: url(/image/check.png);
  background-position: center;
  background-repeat: no-repeat;
}

.wysiwyg-styles {
  @apply text-left;
}
.wysiwyg-styles h3 {
  @apply text-3xl;
}
.wysiwyg-styles strong {
  @apply font-bold;
}
.wysiwyg-styles a {
  @apply underline;
  color: #0000EE;
}
.wysiwyg-styles ul {
  @apply list-disc pl-12;
}
.wysiwyg-styles ol {
  @apply list-decimal pl-12;
}

@tailwind utilities;